<template>
  <div class="test padding-top">
    <ul class="test-ul">
      <li v-for="(item,index) in list" :key="index">
        <cube-button @click="item.fn">{{item.btnName}}</cube-button>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "test",
  components: {},
  data() {
    let that = this;
    return {
      hasBridge: false,
      list: [
        {
          fn() {
            that.call("19999999999").then(res => {
              this.$dialog({
                type: "alert",
                title: "打电话回调",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "打电话"
        },
        {
          fn() {
            that.getCommonParams().then(res => {
              this.$dialog({
                type: "alert",
                title: "获取app通用参数回调",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "获取app通用参数"
        }
      ]
    };
  },
  methods: {},
  mounted: function() {}
};
</script>
<style lang="stylus">
.test {
  > p {
    padding: 0 0.2rem;
    line-height: 0.5rem;
  }

  ul {
    display: flex;
    flex-wrap: wrap;

    li {
      width: 45%;
      margin: 0.1rem 2.5%;

      button {
        width: 100%;
      }
    }
  }
}
</style>